.aspect-ratio(@width, @height) {
	position: relative;
	&:before {
		display: block;
		content: "";
		width: 100%;
		padding-top: (@height / @width) * 100%;
	}
	> .content {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}
	> img.content {
		.square(100%);
	}
}